import React from 'react'
import styled from 'styled-components/macro'

import { mediaDesktop, mediaMobile } from '../app/Responsive'
import { Colors, FontSizes } from '../styling'

interface INotifyMessageProps {
  isError?: boolean
  isVisible: boolean
  onClick?(): void
}

const backgroundProperty = (props: INotifyMessageProps) =>
  props.isError ? Colors.red : Colors.green

const StyledNotifyMessage = styled.div`
  position: fixed;
  border-radius: 1rem;
  top: 0;
  ${mediaDesktop`
  width: 60%;
  left: 20%;
  `};
  ${mediaMobile`
  width: 90%;
  left: 5%;
  `};
  height: 3.9rem;
  opacity: 0.85;
  background: ${backgroundProperty};
  display: flex;
  align-items: center;
  justify-content: center;

  & .notify-message {
    color: white;
    text-align: center;
    margin: 0 0.5rem 0.4rem 0.5rem;
    font-size: ${FontSizes.InfoMessageFontSize};
  }
`

export const NotifyMessage: React.FC<INotifyMessageProps> = ({
  children,
  isVisible,
  isError = false,
  onClick,
  ...props
}) =>
  isVisible ? (
    <StyledNotifyMessage
      onClick={onClick}
      isVisible={isVisible}
      isError={isError}
      {...props}
    >
      <div
        className="notify-message"
        data-testid={`notify${isError ? '-error' : ''}`}
      >
        {children}
      </div>
    </StyledNotifyMessage>
  ) : null
