import { IFormState } from 'react-form'
import { isEmpty } from 'validator'

import { i18n } from '../i18n'

export function isFormValid(formState: IFormState) {
  return !formState.errors || Object.keys(formState.errors).length === 0
}

export function validateIsRequired(fieldName: string) {
  return (value: string) =>
    isEmpty(value + '') ? i18n.t`${fieldName} must be entered` : null
}
