import React from 'react'
import { FormApi } from 'react-form'

import { isFormValid } from '../helpers/form'
import { ITouchButtonProps, TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'

interface IProps extends ITouchButtonProps {}

export const FormSubmit: React.FC<IProps> = ({ children, ...props }) => (
  <FormApi>
    {form => (
      <TouchButton
        color={Colors.green}
        disabled={!isFormValid(form) || form.submitting}
        onClick={e => {
          e.preventDefault()
          form.submitForm(e)
        }}
        {...props}
      >
        {children}
      </TouchButton>
    )}
  </FormApi>
)
