import { observer } from 'mobx-react-lite'
import React from 'react'

import { NotifyMessage } from '../../../atoms/NotifyMessage'
import { TPasswordLoginModel } from '../models/PasswordLoginModel'

interface IPasswordLoginErrorProps {
  model: TPasswordLoginModel
}

export const PasswordLoginError = observer<IPasswordLoginErrorProps>(
  ({ model }) =>
    model.hasFailed ? (
      <NotifyMessage isVisible={true} isError={true}>
        {model.errorMessage}
      </NotifyMessage>
    ) : null,
)
