import React from 'react'
import { Field, FieldType, ValidationResult } from 'react-form'
import { isEmpty } from 'validator'

import { i18n } from '../i18n'
import { FormInputStyled } from './FormInputStyled'

interface IProps extends React.InputHTMLAttributes<never> {
  field: FieldType
  defaultValue?: string
  isRequired?: boolean
  validatioName?: string
  validationMessage?: string
  inputFullWidth?: boolean
  validate?(value: string): ValidationResult
}

export type TFormInputTextBoxProps = IProps

export const FormInputTextBox = React.forwardRef<HTMLInputElement, IProps>(
  (
    {
      field,
      inputFullWidth = false,
      isRequired,
      validatioName,
      validationMessage,
      validate,
      ...props
    },
    inputRef,
  ) => (
    <Field
      field={field}
      validate={
        validate ||
        (value => {
          if (isRequired && isEmpty(value + '')) {
            if (validationMessage) {
              return validationMessage
            }
            return i18n.t`${validatioName} is required`
          }
          return null
        })
      }
    >
      {fieldApi => (
        <FormInputStyled
          value={!fieldApi.value && fieldApi.value !== 0 ? '' : fieldApi.value}
          onChange={ev => {
            if (ev.target.validity.valid) {
              fieldApi.setTouched(true)
              fieldApi.setValue(ev.target.value)
            }
          }}
          inputFullWidth={inputFullWidth}
          invalid={Boolean(fieldApi.error)}
          // @ts-ignore
          ref={inputRef}
          {...props}
        />
      )}
    </Field>
  ),
)
