import { Trans } from '@lingui/macro'
import React from 'react'
import { Form } from 'react-form'

import { FormInputTextBox } from '../../../atoms/FormInputTextBox'
import { MarginBox } from '../../../atoms/MarginBox'
import { i18n } from '../../../i18n'
import { FormSubmit } from '../../../molecules/FormSubmit'
import { Spacing } from '../../../styling'
import { TPasswordLoginModel } from '../models/PasswordLoginModel'

interface IPasswordLoginFormProps {
  model: TPasswordLoginModel
}

interface IFormValues {
  username: string
  password: string
}

export const PasswordLoginForm: React.FC<IPasswordLoginFormProps> = ({
  model,
}) => (
  <Form
    onSubmit={({ username, password }: IFormValues) =>
      model.requestToken(username, password)
    }
    render={formApi => (
      <form onSubmit={formApi.submitForm}>
        <FormInputTextBox
          field="username"
          name="username"
          isRequired={true}
          inputFullWidth={true}
          validatioName={i18n.t`username`}
          autoFocus={true}
          autoCorrect="off"
          autoCapitalize="off"
          spellCheck={false}
          placeholder={i18n.t`username`}
        />

        <FormInputTextBox
          field="password"
          name="password"
          isRequired={true}
          inputFullWidth={true}
          validatioName={i18n.t`password`}
          type="password"
          placeholder={i18n.t`password`}
        />

        <MarginBox margin={`${Spacing.Middle} -0.1rem 0 0.1rem`}>
          <FormSubmit>
            <Trans>Sign-in</Trans>
          </FormSubmit>
        </MarginBox>
      </form>
    )}
  />
)
