import { cn } from '@speedlo/ui'
import styled from 'styled-components/macro'

import { Spacing } from '../styling'

interface IProps {
  invalid?: boolean
  inputFullWidth?: boolean
}

export const FormInputStyled = styled.input.attrs<IProps>(
  ({ invalid = false, inputFullWidth = false }) => ({
    className: cn({ invalid, fullWidth: inputFullWidth }),
  }),
)<IProps>`
  margin: ${Spacing.MarginAroundFormComponents};
  &.invalid {
    border: 1px red solid;
    background-color: #ff000011;
  }
  &.fullWidth {
    width: 100%;
  }
`

// TODO: Rename all uses in separate commit
export { FormInputStyled as StyledFormInput }
