import { useRouter } from '@speedlo/hooks'
import React from 'react'
import { Redirect } from 'react-router-dom'

import { useRoot } from '../../../atoms/RootProvider'
import { PasswordLoginPage } from '../pages/PasswordLoginPage'

export const PasswordLoginRoute: React.FC = () => {
  const { auth } = useRoot()

  const { match } = useRouter<{ token: string; userId: ID }>({
    path: '/login/:token/:userId',
  })

  React.useEffect(() => {
    if (match) {
      auth.authenticate(window.atob(match.params.token), match.params.userId)
    }
  }, [auth, match])

  return auth.isAuthenticatedUser ? (
    <Redirect to="/" />
  ) : (
    <PasswordLoginPage model={auth.provider.passwordLogin} />
  )
}

export default PasswordLoginRoute
